import React, { useState } from 'react';
import { Select, InputNumber, Card, Typography } from 'antd';
import styled from 'styled-components';

const { Title } = Typography;

const Container = styled.div`
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
`;

const FormItem = styled.div`
  margin-bottom: 20px;
  display: flex;
  align-items: center;
`;

const Label = styled.div`
  width: 200px;
  margin-right: 20px;
  font-weight: bold;
`;

const ValueDisplay = styled.div`
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  gap: 24px;
`;

const App: React.FC = () => {
  const [companyType, setCompanyType] = useState<string>('');
  const [cashFlowType, setCashFlowType] = useState<string>('');
  const [freeCashFlow, setFreeCashFlow] = useState<number>(0);
  const [growthRate, setGrowthRate] = useState<number>(10);
  const [minPE, setMinPE] = useState<number>(25);
  const [maxPE, setMaxPE] = useState<number>(30);

  const calculateLowValue = () => {
    const pe = minPE;
    const growth = (1 + growthRate / 100) ** 3;
    const peWithGrowth = Math.min(pe * growth / 2, pe * 0.7);
    return (freeCashFlow * peWithGrowth).toFixed(2);
  };

  const calculateHighValue = () => {
    return (freeCashFlow * 50).toFixed(2);
  };

  return (
    <Container>
      <Title level={2}></Title>
      <Card>
        <FormItem>
          <Label></Label>
          <Select
            style={{ width: 200 }}
            value={companyType}
            onChange={setCompanyType}
            options={[
              { value: '', label: '' },
            ]}
          />
        </FormItem>

        <FormItem>
          <Label></Label>
          <Select
            style={{ width: 200 }}
            value={cashFlowType}
            onChange={setCashFlowType}
            options={[
              { value: '', label: '' },
              { value: '', label: '' },
            ]}
          />
        </FormItem>

        <FormItem>
          <Label></Label>
          <InputNumber
            style={{ width: 200 }}
            value={freeCashFlow}
            onChange={(value) => setFreeCashFlow(value || 0)}
          />
        </FormItem>

        <FormItem>
          <Label> (%)</Label>
          <InputNumber
            style={{ width: 200 }}
            value={growthRate}
            onChange={(value) => setGrowthRate(value || 0)}
            min={10}
            max={20}
          />
        </FormItem>

        <FormItem>
          <Label></Label>
          <InputNumber
            style={{ width: 95 }}
            value={minPE}
            onChange={(value) => setMinPE(value || 0)}
          />
          <span style={{ margin: '0 10px' }}>~</span>
          <InputNumber
            style={{ width: 95 }}
            value={maxPE}
            onChange={(value) => setMaxPE(value || 0)}
          />
        </FormItem>

        <ValueDisplay>
          <div>
            <Title level={4}></Title>
            <div style={{ color: '#ff4d4f', fontSize: '20px' }}>
              {calculateLowValue()}
            </div>
          </div>
          <div>
            <Title level={4}></Title>
            <div style={{ color: '#52c41a', fontSize: '20px' }}>
              {calculateHighValue()}
            </div>
          </div>
          <div>
            <Label>Min *Min * 1 + ^ 3, Min* 0.7</Label>
            <Label> * 50 </Label>
          </div>
        </ValueDisplay>
      </Card>
    </Container>
  );
};

export default App; 